export default {
    name: 'NewsSwiper',
    data(){
        return{
            swiperOption: {
                slidesPerView: 2,
                spaceBetween: 30,
                breakpoints: {
                    1280: {
                        slidesPerView: 2,
                        spaceBetween: 30,
                    },
                    320: {
                        slidesPerView: 1,
                        spaceBetween: 0,
                    }
                },
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                }
            },
            testData: []
        }
    },
    methods: {
        goToNewsContent(id) {
            this.$router.push(`/beautyShare/${id}`);
        },
        openNewTab(id) {
            const routerData = this.$router.resolve({path: `/beautyShare/${id}`});
            window.open(routerData.href, '_blank');
        },
    },
    mounted() {
        let arr = [1,2,3,4,5,6];
        arr.forEach((item)=>{
            this.testData.push({
                id: item,
                imgsrc: require('~/assets/img/beauty-share.jpg'),
                title: '',
                detail: '<p></p><p></p> <p>() </p>'
            })
        })
        console.log(this.testData);
    }
}
