export default {
    name: 'NewsSwiper',
    data(){
        return{
            swiperOption: {
                slidesPerView: 3,
                spaceBetween: 30,
                breakpoints: {
                    1024: {
                        slidesPerView: 2,
                        spaceBetween: 15,
                    },
                    500: {
                        slidesPerView: 1,
                        spaceBetween: 0,
                    }
                },
                navigation: {
                    nextEl: '.news-swiper-outside .swiper-button-next',
                    prevEl: '.news-swiper-outside .swiper-button-prev'
                }
            },
            testData: []
        }
    },
    methods: {
        scrollToPageTop() {
            window.scrollTo({ 
                top: 0, 
                behavior: "smooth" 
            });
        },
        goToNewsContent(id) {
            this.$router.push(`/maxBeauty/news/${id}`);
            this.scrollToPageTop()
        },
        openNewTab(id) {
            const routerData = this.$router.resolve({path: `/maxBeauty/news/${id}`});
            window.open(routerData.href, '_blank');
        },
    },
    mounted() {
        let arr = [1,2,3,4,5,6];
        arr.forEach((item)=>{
            this.testData.push({
                id: item,
                imgsrc: require('~/assets/img/news.jpg'),
                time: `2021-03-1${item}`,
                title: '  ',
                detail: ''
            })
        })
    }
}
